* {
  box-sizing: border-box;
}

@font-face {
  font-family: Avenir, sans-serif;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir, sans-serif;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
}

html {
  --border-color: #e1e7ed;
  --main-color: #3d4e61;
  --background-color: #fff;
  --box-shadow-color: #3d4e611a;
}

.page {
  font-family: Avenir, sans-serif;
}

.page__form-container {
  margin-top: 20px;
}

.form__container {
  position: relative;
}

.form__search-bar {
  border: 1px solid var(--border-color);
  background-color: var(--background-color);
  box-shadow: 0 1px 8px 0 var(--box-shadow-color);
  color: var(--main-color);
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  display: block;
}

.form__search-bar::placeholder {
  color: var(--main-color);
}

.form__search-bar--big {
  height: 70px;
  padding-left: 62px;
}

.form__search-bar--small {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.form__search-bar--small:hover {
  box-shadow: 0 3px 8px 0 var(--box-shadow-color);
}

.form__search-bar--big:hover, .form__search-bar--big:focus {
  box-shadow: 0 4px 4px #00000040;
}

.form__search-bar:focus {
  background: linear-gradient(180deg, var(--background-color) 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 900;
}

.form__icon {
  background-image: url("Search.ccd3c07b.svg");
  position: absolute;
}

.form__icon--big {
  width: 19px;
  height: 19px;
  top: 26px;
  left: 26px;
}

.form__icon--small {
  background-size: 11px;
  width: 11px;
  height: 11px;
  top: 16px;
  left: 13px;
}
